const MapMobile = () => {
  const projection = geoEqualEarth()
  .scale(160)
  .translate([ 800 / 2, 450 / 2 + 50])
  const [geographies, setGeographies] = useState([])
  const map = React.createRef();
  useEffect(() => {
    fetch("/world-110m.json")
      .then(response => {
        if (response.status !== 200) {
          console.log(`There was a problem: ${response.status}`)
          return
        }
        response.json().then(worlddata => {
          setGeographies(feature(worlddata, worlddata.objects.countries).features)
        })
      })
  }, [])

  return (
    <>
      <div className="module map snap mobile" style={{display: 'none'}}>
        <h2>
          我們的記者，深入全球新聞現場
        </h2>
        <div className="section_sub">
          <div className="map__wrapper">
            <svg style={{ width: `100%`, height: `auto`}} viewBox="0 0 800 450" ref={map}>
              <g className="countries">
                {
                  geographies.map((d,i) => (
                    <path
                      key={ `path-${ i }` }
                      d={ geoPath().projection(projection)(d) }
                      className="country"
                      fill={`rgba(255,255,255,0.2)`}
                      stroke="#222222"
                      strokeWidth={ 0.3 }
                    />
                  ))
                }
              </g>
            </svg>
          </div>
          <div className="map__context">
            {
              reporters.map((reporter,i) => (
              <div
                key={ `path-${ i }` }
                className="map__item"
              >
                <span /><h3>{reporter.name}</h3>
              </div>
            ))
            }
          </div>
        </div>
      </div>
      <div className="module map snap mobile" style={{ pointerEvents: 'none'}}>
        <h2 className="map__secondtitle">我們的會員，遍佈各地華文社群</h2>
        <div className="map__context">
          {
            readers.map((reader,i) => (
            <div
              key={ `reader-${ i }` }
              className="map__item reader"
            >
              <span /><h3>{reader.name}</h3>
            </div>
          ))
          }
        </div>
        <div className="map__number">
          <div>截至2020年4月 會員人數達</div>
          <div>50,000+</div>
        </div>
      </div>
    </>
  )
}

export default MapMobile