const readers = [
  { name: "美國", coordinates: [-83.5862,33.50644], size: 20 },
  { name: "加拿大", coordinates: [-73.8339417,56.173123], size: 15 },
  { name: "中國大陸", coordinates: [105.7641575,39.6427201], size: 25 },
  { name: "日本", coordinates: [142.67822,46.8383312], size: 10 },
  { name: "荷蘭", coordinates: [6.588825,66.7165095], size: 10 },
  { name: "德國", coordinates: [25.5614813,54.1534976], size: 10 },
  { name: "英國", coordinates: [-10.6025812,57.1279569], size: 10 },
  { name: "法國", coordinates: [6.7143839,44.9841349], size: 10 },
  { name: "澳洲", coordinates: [133.775136,-25.274398], size: 15 },
  { name: "新加坡", coordinates: [104.0300763,-1.8078716], size: 20 },
  { name: "馬來西亞", coordinates: [99.8113263,8.9655619], size: 20 },
  { name: "台灣", coordinates: [131.5166541,25.9118929], size: 30 },
  { name: "香港", coordinates: [108.53293,24.20873], size: 30 },
  { name: "澳門", coordinates: [118.7822791,19.0966794], size: 10 },
  { name: "南韓", coordinates: [128.4502478,37.2594406], size: 15 },
  { name: "菲律賓", coordinates: [121.9701696,8.1496029], size: 10 },
]

const Map = () => {
  const projection = geoEqualEarth()
  .scale(160)
  .translate([ 800 / 2, 450 / 2 + 50])
  const [geographies, setGeographies] = useState([])
  const map = React.createRef();
  const marker = React.createRef();
  const reader = React.createRef();
  const caption = React.createRef();
  const reporter_title = React.createRef();
  const reader_title = React.createRef();
  const [animation, setAnimation] = useState(true);
  useEffect(() => {
    fetch("/world-110m.json")
      .then(response => {
        if (response.status !== 200) {
          console.log(`There was a problem: ${response.status}`)
          return
        }
        response.json().then(worlddata => {
          setGeographies(feature(worlddata, worlddata.objects.countries).features)
        })
      })
    let tl = gsap.timeline({scrollTrigger:{
      trigger: map.current,
      scrub: true,
      start: "top top",
      end: "+=100%",
      invalidateOnRefresh: true
    }})
    tl.to(marker.current, 0.4, {opacity: 0, ease: "Sine.easeIn"}, 0)
      .to(reporter_title.current, 0.4, {opacity: 0, ease: "Sine.easeIn"}, 0)
      .fromTo(reader.current, 0.4, {opacity: 0}, {opacity: 1}, 0.4)
      .fromTo(reader_title.current, 0.4, {opacity: 0}, {opacity: 1}, 0.4)
      .to(marker.current, 0, {display: "none"}, 1)
  }, [])

  const handleCountryClick = countryIndex => {
    console.log("Clicked on country: ", geographies[countryIndex])
  }

  const handleMarkerClick = i => {
    console.log("Marker: ", reporters[i])
  }

  return (
    <>
      <div className="module map snap desktop" ref={map}>
        {/* <canvas width="1600" height="900"></canvas> */}
        <div className="section_sub">
          <div className="map__wrapper">
            <h2>
            <span ref={reporter_title}>我們的記者，深入全球新聞現場</span>
            <span ref={reader_title}>我們的會員，遍佈各地華文社群</span>
          </h2>
            <svg viewBox="0 0 800 450">
              <g className="countries">
                {
                  geographies.map((d,i) => (
                    <path
                      key={ `path-${ i }` }
                      d={ geoPath().projection(projection)(d) }
                      className="country"
                      fill={`rgba(255,255,255,0.2)`}
                      stroke="#222222"
                      strokeWidth={ 0.3 }
                      onClick={ () => handleCountryClick(i) }
                    />
                  ))
                }
              </g>
              <g className="readers" ref={reader}>
                {
                  readers.map((reader, i) => (
                    <g key={ `marker-${i}` }>
                      <circle
                        cx={ projection(reader.coordinates)[0] }
                        cy={ projection(reader.coordinates)[1] }
                        style={{
                          transformOrigin: projection(reader.coordinates)[0] + 'px ' + projection(reader.coordinates)[1] + 'px',
                          animationDuration: 4 + Math.random() * 1 + 's'
                        }}
                        r={ reader.size }
                        fill={`rgba(16,231,255,0.4)`}
                        className="marker"
                        onClick={ () => handleMarkerClick(i) }
                      />
                      <text
                        key={ `text-${i}` }
                        x={ projection(reader.coordinates)[0] }
                        y={ projection(reader.coordinates)[1] }
                        fontSize={ '0.5em' }
                        fill={ '#fff' }
                        transform = {"translate(-" + reader.name.length * 4 + ",2)"}
                      >{reader.name}</text>
                    </g>
                  ))
                }
              </g>
              <g className="markers" ref={marker}>
                {
                  reporters.map((reporter, i) => (
                    <g key={ `marker-${i}` } className={i === 25 && animation ? "active" : ""}>
                      <circle
                        cx={ projection(reporter.coordinates)[0] }
                        cy={ projection(reporter.coordinates)[1] }
                        r={ 5 }
                        style={{
                          transformOrigin: projection(reporter.coordinates)[0] + 'px ' + projection(reporter.coordinates)[1] + 'px',
                        }}
                        fill="#FACC29"
                        className="marker"
                        onClick={ () => handleMarkerClick(i) }
                        onMouseEnter={() => setAnimation(false)}
                      />
                      <text
                        key={ `text-${i}` }
                        x={ projection(reporter.coordinates)[0] }
                        y={ projection(reporter.coordinates)[1] }
                        fontSize={ '0.5em' }
                        fill={ '#222' }
                        transform = {"translate(-" + reporter.name.length * 4 + ",2)"}
                      >{reporter.name}</text>
                    </g>
                  ))
                }
              </g>
            </svg>
          </div>
        </div>
      </div>
      <div className="module map snap desktop" style={{ pointerEvents: 'none'}}>
        <div className="map__number" ref={caption}>
          <div>截至2020年4月 會員人數達</div>
          <div>50,000+</div>
        </div>
      </div>
    </>
  )
}

export default Map