import React, { useEffect, useState }from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import "./Quote.scss"

const Quote = ({ autoplay }) => {
  const [nav1, setNav1] = useState(null);
  const [nav2, setNav2] = useState(null);

  const slider = React.createRef();
  const handler = React.createRef();
  const quote = React.createRef();

  const quotaData = [
    { content: "當我對眼前華文世界的一些議題感到迷惘，想深入了解，或找個說法，我都會帶著期待上《端傳媒》— 現在它就是做到這麼無可替代！", author: "陳冠中", title: "作家" },
    { content: "好記者是傳媒最重要的資產。端有一群熱誠、勇敢、專業、認真的年輕記者。每次接觸他們，都令我深深感到鼓舞，對新聞自由得到保衛充滿信心。", author: "吳靄儀", title: "香港執業大律師" },
    { content: "對我而言，「端」肯定不是最全面最多元的信息來源，但它卻是信息質量最整齊，評論水準也最有保證的觀點依據。", author: "梁文道", title: "作家" },
    { content: "這些年來，端傳媒的報導具深度而不失時效，評論具國際視野而不失本地觸覺，端傳媒絕對是一個優質網媒。", author: "李立峯", title: "香港中文大學新聞與傳播學院院長" },
    { content: "《端傳媒》以宏觀的國際視野，以及勇於衝撞時代的哪吒烈焰，注視著兩岸三地瞬時萬變的狀態，我寄予長遠的期待與祝福。", author: "阮慶岳", title: "作家、建築師" },
    { content: "「端」讓我想到「極端」和「尖端」。前者是往問題的最深最盡處挖掘，後者是在浪尖裡攫住一抹光。", author: "韓麗珠", title: "作家" },
    { content: "端的年輕寫手，在報導事物時跟老一輩截然不同，經常會有更加合宜的當代思維，切進事件本質，完成非單一觀點的闡述。這種成熟，常讓我覺得自己的時代已經過去。　", author: "劉克襄", title: "作家" },
    { content: "我遇見端是偶然，只消一篇文章就被牢牢吸引，從此同端走過四年多，在外曾看到端的低潮，在裡也見證過端取得的可喜成績。", author: "Enzo Chen", title: "端傳媒會員" }
  ]

  const reOrder = (array) => {
    array.unshift(array.pop());
    array.unshift(array.pop());
    return array
  }

  useEffect(() => {
    if (autoplay) {
      slider.current.slickPlay()
    }
  }, [autoplay])

  useEffect(() => {
    const timer = setTimeout(() => {
      setNav1(slider.current)
      setNav2(handler.current)
    }, 1000);
    return () => clearTimeout(timer);
  }, [handler, slider, quote])

  return (
    <div className="quote module snap" ref={quote}>
      <h2>他們都在讀端</h2>
      <Slider
        asNavFor={nav2}
        ref={slider}
        slidesToShow={1}
        swipeToSlide={true}
        focusOnSelect={false}
        arrows={true}
        swipe={true}
        autoplay={false}
        autoplaySpeed={4000}
        className="quote__slider"
      >
        {
          quotaData.map((quota, i) => (
          <div key={ `quota-${i}` } className="quote__content">
            <h3>{quota.content}</h3>
            <span className="quote__author">{quota.title}<br/>{quota.author}</span>
          </div>
          ))
        }
      </Slider>
      <Slider
        asNavFor={nav1}
        ref={handler}
        slidesToShow={5}
        swipeToSlide={true}
        focusOnSelect={false}
        vertical={true}
        verticalSwiping={true}
        arrows={false}
        className="quote__handler"
      >
        {
          reOrder(quotaData).map((quota, i) => (
          <div key={ `handler-${i}` } className="quote__pagination">
            <div>{quota.author}</div>
          </div>
          ))
        }
      </Slider>
      
    </div>
  );
}

export default Quote;