import React, { useEffect, useState } from "react"
import { gsap } from 'gsap'
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Slider from './Components/Slider';
import Cover from './Components/Cover';
import Membership from './Components/Membership';
import Map from './Components/Map';
import MapMobile from './Components/MapMobile';
import Quote from './Components/Quote';
import Honour from './Components/Honour';
import Footer from './Components/Footer';
import logo from './Images/logo.svg'
import useWindowSize from "./useWindowSize";
import './App.scss';

function App() {
  const windowSize = useWindowSize();
  const [autoplay, setAutoplay] = useState(false);
  useEffect(() => {
    let sections = gsap.utils.toArray(".snap");
    sections.forEach((eachPanel, index) => {
      ScrollTrigger.create({
        trigger: eachPanel,
        start: "top top",
        scrub: 1,
        snap: {
          snapTo: 1,
          duration: { min: 0.3, max: 0.7 },
          ease: "power2.inOut",
          anticipatePin: 0.2,
          delay: 0.5
        },
        onUpdate: self => {
          // let index = Math.round(self.progress * (sections.length - 1))
          if (self.progress === 1 & index === 2) {
            setAutoplay(true)
          }
        },
        // snap: windowSize === "sm" ? false : {
        //   snapTo: 1,
        //   duration: { min: 0.3, max: 0.7 },
        //   ease: "power2.inOut",
        //   anticipatePin: 0.2,
        //   delay: 0.2
        // },
        invalidateOnRefresh: true
      });
    });
  }, []);
  return (
    <div className="App">
      <a href="https://theinitium.com" className="App-logo"><img src={logo} alt="logo" /></a>
      <Cover id="cover" />
      <Slider />
      <Membership />
      { windowSize === "sm" ?  <MapMobile /> : <Map /> }
      <Quote autoplay={autoplay} />
      <Honour />
      <Footer />
    </div>
  );
}

export default App;
